<template>
	<div class='music-list'>
		
	    <div class="back" @click='back'>
           <i class="icon-back"></i>
        </div>
        
        <h1 class="title" v-text="title"></h1>
        <div class="bg-image" :style="bigimage" ref='img'>
        	<div class="play-wrapper">
        		<div class="play" ref='random'>
        			<span class="text" >点击下面列表播放</span>
        		</div>
        	</div>
        	
        </div>
      	 
        <div class="bg-layer" ref='layer'></div>
        <scroll :data='songs' @scroll='scroll' :probe-type='probetype' :listen-scroll='listenscroll' class='list' ref='lists'>
          <div class="song-list-wrapper">	
            <songlist :song='songs' @select='selectItem'></songlist>
          </div>
         <div class="loading-container" v-if='!songs.length'>
        	<loading></loading>
         </div>
        </scroll>
         
	</div>
        
</template>

<script>
	import songlist from 'base/songlist/songlist'
	import scroll from 'base/scroll/scroll'
	import loading from 'base/loading/loading'
	import {mapActions} from 'vuex'
	import {playlistMixxin} from 'common/js/mixin'
	export default{
   
		data(){
			
			return{
				
				probetype:3,
				listenscroll:true,
				scrollY:''
			}
		},
		props:{
			bgImage:{
				type:String,
				default:''
				
			},
			songs:{
				type:Array,
				default:[]
				
			},
			title:{
				type:String,
				default:''
				}
		},
		computed:{
			bigimage(){
				
				return `background-image:url(${this.bgImage})`
			}
			
			},
			
			created(){
		
			},
			components:{
				songlist,
				scroll,
				loading 
				
			},
			mounted(){
			  
			 this.height=this.$refs.img.clientHeight
			 this.max=-this.height
			 this.$refs.lists.$el.style.top=`${this.height}px`
				
			},
			methods:{
				
				scroll(pos){
					
					this.scrollY=pos.y
					
				},
				back(){
					
					this.$router.back()
			
					
				},
				selectItem(item,index){
					
					this.selectPlay({
						list:this.songs,
						index
					})
				
				},
				...mapActions([
					'selectPlay'
					
				])
				
			},
			watch:{
				
				scrollY(newy){
				  this.bigmax=Math.max(this.max,newy)
				  this.$refs.layer.style.transform=`translateY(${this.bigmax+40}px)`
				  
				  if(newy<this.max){
				  	  this.$refs.img.style.zIndex=10
				      this.$refs.img.style.paddingTop=0
				      this.$refs.img.style.height=40+'px'
				      
				  }else{
				  	  this.$refs.img.style.paddingTop=70+'%'
				  	  this.$refs.img.style.zIndex=0
				      this.$refs.img.style.height=0
				  	  }	
				  if(newy>0){
				  	const percent=Math.abs(newy/this.height)
				  	let scale=1+percent
				  	this.$refs.img.style.transform=`scale(${scale})`
				  	this.$refs.img.style.zIndex=10
				  	
				  }
				  if(newy<0){
				  	this.$refs.random.style.display='none'
				  	
				  	}else{
				  	this.$refs.random.style.display='block'	
				  		
				  	}
				 
				}
				
				
			}
		}
		  
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
   @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: #ffffff
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid #ffffff
          color: #ffffff
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .list
      position: fixed
      top: 0
      bottom: 500px
      width: 100%
      background: $color-background
 
      .song-list-wrapper
        padding: 20px 30px
        
      .loading-container
        position:absolute
        width: 100%
        top: 20%
        transform: translateY(-50%)
      
       
</style>


